.layer {
    position:absolute;
    width:96.5%;
    height:96.5%;
    border-radius: 50%;
    border:10px solid var(--gray);
    top:1.8%;
    right:1.8%;
    z-index: -50;
}
canvas {
    position: relative;
}
.numerator {
    color:var(--side-bg);
    font-size: 1.9rem;
    position: absolute;
    bottom:0;
    right:45%;

}
.denominator {
    color: var(--donut-denominator);
    font-size: .95rem;
    position: absolute;
    top:90%;
    left:70%;
 
}
hr {
    transform: rotate(-45deg);
    width:110px;
    height:5.5px;
    background-color: var(--heading);
    border:.5px solid var(--heading);
    margin:30px 0px  0 40px;
    box-shadow: 2px 2px 10px #00000040;
   
}
@media screen and (max-width: 767px) {
    .layer {
        border:7.5px solid var(--gray);
    }
    hr {
        width : 80px;
        margin:20px 0px  0 20px;
    }
  }
  @media screen and (max-width :576px) {
    .layer {
        top:1.75%;
        right:1.5%;
        
    }
    hr {
        height:5px;
    }
    
  }