.overview-holder{
    width:92%;
    margin:0 auto;
    padding-top: 5.5%;
    color:var(--side-bg);
    height:100%;
}
.overview-holder h1,
.overview-holder h2,
.overview-holder h3 {
    font-family: 'Montserrat';
}
.breakdown h3 {
    font-family: 'Roboto';
}


.flex-holder {
    display: flex;
    flex-wrap: wrap;
}
.segment {
    width:50%;
}
.segment .header {
    margin:10% 0 5% 0
}
.details  div {
    display: flex;
}

.details h2 {
    width:40%;
    padding-bottom: 10%;
} 

.details p {
    width:50%;
    
}
.donut-chart {
    height:232px;
    width:232px;
    -ms-transform: rotate(90deg); 
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    font-family: 'Montserrat';
    margin:10% 0 0 19%;
    cursor: pointer;
    /* position: relative; */
} 
@media screen and (max-width: 767px) {
    .segment {
        width:100%;
    }
   .donut-chart {
    height:180px;
    width:180px;
    margin:0 0 10% 40%
   }
  }
  @media screen and (max-width: 576px) {
   
    .donut-chart {
        height:140px;
        width:140px;
       }
  }